import React from "react";

export default function Info({ protect }) {
  return (
    <div className="flex column">
      <div className="flex gap-10px">
        <label htmlFor="attack">attack</label>
        <input
          className=" flex justify-center align-center"
          id="attack"
          type="range"
          min="0"
          max="10"
          value={protect.info.attack}
          readOnly
        />
        {protect.info.attack}
      </div>
      <div className="flex gap-10px">
        <label htmlFor="defense">defense</label>
        <input
          className=" flex justify-center align-center"
          id="defense"
          type="range"
          min="0"
          max="10"
          value={protect.info.defense}
          readOnly
        />
        {protect.info.defense}
      </div>
      <div className="flex gap-10px">
        <label htmlFor="magic">magic</label>
        <input
          className=" flex justify-center align-center"
          id="magic"
          type="range"
          min="0"
          max="10"
          value={protect.info.magic}
          readOnly
        />
        {protect.info.magic}
      </div>
      <div className="flex gap-10px">
        <label htmlFor="difficulty">difficulty</label>
        <input
          className=" flex justify-center align-center"
          id="difficulty"
          type="range"
          min="0"
          max="10"
          value={protect.info.difficulty}
          readOnly
        />
        {protect.info.difficulty}
      </div>
    </div>
  );
}
