import React, { useEffect } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { loadGigs } from "../store/gig.action";
import { CardPreview } from "../cmps/card-preview";
import LoaderExampleLoader from "../cmps/loder";

export const CardList = ({ filterBy }) => {
  const protects = useSelector((state) => state.gigModule.prodcs);
  const dispatch = useDispatch();
  //    async componentDidMount(){
  // }

  useEffect(() => {
    dispatch(loadGigs());
  }, []);
  console.log("filterBy from cmp ", filterBy);
  if (protects.length === 0) return <LoaderExampleLoader />;
  let filter = protects.map((pro) =>
    pro.filter((pros) => pros.name.includes(filterBy))
  );
  return (
    <div className="card-grid">
      {!filterBy
        ? protects.map((pro) =>
            pro.map(
              (pros) => <CardPreview key={pros.id} protect={pros} />
              // console.log(" pros", pros)
            )
          )
        : filter.map(
            (pros) =>
              pros.map((pro) => <CardPreview key={pro.id} protect={pro} />)
            // <CardPreview key={pro.id} protect={pro} />
          )}
      {/* {protects.map((pro) =>
        pro.map(
          (pros) => <CardPreview key={pros.id} protect={pros} />
          // console.log(" pros", pros)
        )
      )} */}
      {/* {filter.map(
        (pros) => pros.map((pro) => <CardPreview key={pro.id} protect={pro} />)
        // <CardPreview key={pro.id} protect={pro} />
      )} */}
    </div>
  );
};

