import { storageService } from "./async-storage.service.js";
import { utilService } from "./util.service.js";
// import { userService } from './user.service.js'

import axios from "axios";
import { httpService } from "./http.service.js";

const STORAGE_KEY = "prorct";
// const listeners = []
const RATE_URL =
  "https://ddragon.leagueoflegends.com/cdn/11.22.1/data/en_US/champion.json";

export const protectService = {
  query,
  getById,
  save,
  remove,
  getEmptyCar,
  queryCmp,
};

// function query() {
//     return _createDefaultCars();
// }

async function query() {
  var cars = await storageService.query(STORAGE_KEY);

  // console.log(cars);
  if (!cars || !cars.length) {
    //     cars = _createDefaultCars();
    var cars = await axios(RATE_URL);
    cars = cars.data.data;
    var carArry = Object.keys(cars).map((key) => cars[key]);
    // console.log(carArry);
    await storageService.post(STORAGE_KEY, carArry);
  }
  return cars;
}

async function queryCmp(cmp) {
  var cmps = await axios(
    `https://ddragon.leagueoflegends.com/cdn/11.22.1/data/en_US/champion/${cmp.id}.json`
  );
  cmps = cmps.data.data;
  return cmps;
}

function getById(carId) {
  return storageService.get(STORAGE_KEY, carId);
}
function remove(carId) {
  // return new Promise((resolve, reject) => {
  //     setTimeout(reject, 2000)
  // })
  // return Promise.reject('Not now!');
  return storageService.remove(STORAGE_KEY, carId);
}
function save(car) {
  if (car._id) {
    return storageService.put(STORAGE_KEY, car);
  } else {
    return storageService.post(STORAGE_KEY, car);
  }
}

function getEmptyCar() {
  return {
    vendor: "Susita-" + (Date.now() % 1000),
    price: utilService.getRandomIntInclusive(1000, 9000),
  };
}

