import React, { useState, useEffect } from "react";
import Info from "../cmps/start";
import { protectService } from "../services/car.service";
import LoaderExampleLoader from "../cmps/loder";

export function Ptodect() {
  const [protect, setProtect] = useState(null);
  const [cmpFromApi, setCmpFromApi] = useState(null);

  useEffect(() => {
    getProtcrt();
  }, []);

  const getProtcrt = async () => {
    const urlParams = new URLSearchParams(window.location.search);
    const protectId = urlParams.get("id");
    const protect = await protectService.getById(protectId);
    const Apicmp = await protectService.queryCmp(protect);
    setProtect((prevprotect) => (prevprotect = protect));
    setCmpFromApi((prev) => (prev = Apicmp));
  };

  if (!cmpFromApi) return <LoaderExampleLoader />;
  const cmp = Object.values(cmpFromApi)[0];
  return (
    <section className="prodect-continer flex">
      <div className="flex column">
        <div className=" info-continer flex">
          <div className="prodect-img-continer">
            <img
              src={`http://ddragon.leagueoflegends.com/cdn/img/champion/loading/${cmp.id}_0.jpg`}
              alt="protect.jpg"
            />
          </div>
          <div className="prodect-info-continer ">
            <h1>{cmp.name}</h1>
            <h2>{cmp.title}</h2>
            <div className="prodect-star-continer flex">
              {/* <p>{protect.rating.rate} </p> */}
              {/* <CustomizedRating rate={protect.rating.rate} /> */}
            </div>
            <div className="prodect-description-price-continer">
              <p>{cmp.lore}</p>
              <Info protect={cmp} />

              <div className="flex column">
                <div className="flex gap-10px">
                  <label htmlFor="hp">hp:</label>
                  {cmp.stats.hp}
                </div>
                <div className="flex gap-10px">
                  <label htmlFor="defense">mp:</label>
                  {cmp.stats.mp}
                </div>
                <div className="flex gap-10px">
                  <label htmlFor="armor">armor:</label>
                  {cmp.stats.armor}
                </div>
                <div className="flex gap-10px">
                  <label htmlFor="difficulty">movespeed:</label>
                  {cmp.stats.movespeed}
                </div>
                <p> tags: </p>
                <ul>
                  {cmp.tags.map((tag) => (
                    <li>{tag}</li>
                  ))}
                </ul>
                <p> enemytips: </p>
                <ul>
                  {cmp.enemytips.map((enemytip) => (
                    <li>{enemytip}</li>
                  ))}
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div className="skins-continer">
          <p>Skins:</p>
          <div className="card-grid ">
            {cmp.skins.map((skin) => (
              <img
                src={`http://ddragon.leagueoflegends.com/cdn/img/champion/splash/${cmp.id}_${skin.num}.jpg`}
              />
            ))}
            {console.log("cmp", cmpFromApi)}
          </div>
        </div>
      </div>
    </section>
  );
}

