import React, { useState, useEffect } from "react";
import { CardList } from "../cmps/card-list.jsx";

export function HomePage() {
  const [filterBy, setfilterBy] = useState(null);

  const handleChange = (ev) => {
    let val = ev.target.value;
    console.log(val);
    setfilterBy((prv) => (prv = val));
  };

  return (
    <section>
      <section className="hero">
        <div className="img-continer">
          <img
            src="https://media.wired.co.uk/photos/606d9e7fdbc4c121710a3f7f/master/w_1600%2Cc_limit/4.jpg "
            alt="hreo.jpg"
          />
        </div>
      </section>
      srech: <input type="text" onChange={handleChange} />
      <section className="card-continer">
        <CardList filterBy={filterBy} />
      </section>
    </section>
  );
}
