import React, { useState } from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { Link } from "react-router-dom";
// import {ComputerIcon,FavoriteIcon,FavoriteBorderIcon,StyledRating} from '@mui/icons-material/Computer';
import CustomizedRating from "../cmps/start";

export function CardPreview({ protect }) {
  const [display, setDisplay] = useState("none");

  const toglle = () => {
    let displayToShow = display === "none" ? "block" : "none";
    setDisplay((prevDisply) => (prevDisply = displayToShow));
  };

  return (
    <Card sx={{ maxWidth: 345 }}>
      <Link to={`/protects/?id=${protect.id}`}>
        <CardMedia
          component="img"
          height="150"
          image={`http://ddragon.leagueoflegends.com/cdn/11.22.1/img/champion/${protect.image.full}`}
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            {protect.name}
          </Typography>
          {/* 
        <Typography gutterBottom variant="text" component="div" color="#faaf00">
         <span>Rate:</span> {protect.rating.rate}
          <div className="comouter-icon"> */}
          {/* <ComputerIcon fontSize="small" color="primary"/> */}
          {/* <CustomizedRating rate={protect.title} size={'small'}/>
            </div> */}
          {/* </Typography>
        <Typography style={{display:display}} variant="body2" color="text.secondary">
        {protect.description}
        </Typography>
        <div className="space"></div>
        <Typography variant="card-price"  component="div" color="text">
        ${protect.price}
        </Typography> */}
        </CardContent>
      </Link>
      <CardActions>
        {/* <Button size="small">Share</Button> */}
        <Button onClick={toglle} size="small">
          {display === "block" ? "Close" : "Learn More"}
        </Button>
      </CardActions>
    </Card>
  );
}
